.toggle-label {
  transition: all 0.2s ease;
  position: relative;
  display: block;
  width: 100%;
  padding: 23px 54px 23px 15px;
  font-weight: 400;
  font-size: 15px;
  line-height: 17px;
  color: #000;
  background-color: #fff;
  border-radius: 5px;
  z-index: 10;
}

.toggle-label:hover {
  cursor: pointer;
}

.toggle-label:hover::before {
  opacity: 0.75;
}

.toggle-label:active::before {
  opacity: 0.5;
