.toggle-label::before {
  transition: all 0.2s ease;
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  height: 24px;
  width: 24px;
  transform: translateY(-50%) rotate(45deg);
  background-image: url("../img/search-close.svg");
}

