  }
  .process__image {
    width: 100%;
    min-width: 100%;
    padding-top: 66.6%;
    margin: 0 0 97px;
  }
  .process__item-desc {
    text-align: center;
    padding-top: 0;
  }
  .process__item-number {
    display: none;
  }
  .process__pagination {
    bottom: auto !important;
    top: calc(95.8vw*0.666 + 15px);
  }
  .process__number {
    position: absolute;
    top: calc(95.8vw*0.666 + 58px);
    left: 50%;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -12px;
    font-size: 14px;
    line-height: 14px;
    color: #fff;
    background-color: #5EA9FF;
    border-radius: 50%;
  }
}

@media (max-width: 479px) {
  .process__item-content {
    padding-left: 3.1%;
    padding-right: 3.1%;
  }
  .process__image img {
    border-radius: 0;
  }
  .process__pagination {
    top: calc(100vw*0.666 + 15px);
  }
  .process__number {
    top: calc(100vw*0.666 + 58px);
  }
}

.manual {
  margin-bottom: 40px;
}

.manual .toggle-wrapper {
  background-color: #fff;
}

.manual .toggle-wrapper > .how-works__label {
  position: relative !important;
  z-index: 10;
}

@media (max-width: 639px) {
  .manual {
    width: 100vw;
    margin-left: -16px;
  }
}

.manual__installation {
  display: block;
  margin: 0 auto;
  opacity: 0;
  position: absolute;
  left: calc(100vh + 1000px);
}

.toggle-input:checked + .toggle-label + .toggle-content {
  display: block;
  opacity: 1;
  position: relative;
  top: auto;
  left: auto;
  z-index: 100;
}

.add-model {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}

.add-model--sended {
  display: flex;
  height: 100%;
}

.add-model__title {
  display: block;
  margin: 0;
  margin-bottom: 25px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  color: #707070;
}

.add-model__desc {
  margin: 0;
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  color: #707070;
  opacity: 0.5;
}

.add-model__container {
  max-width: 636px;
  padding: 0 20px;
  margin: 0 auto;
}

.add-model__text-label {
  position: relative;
  display: block;
  margin-bottom: 15px;
}

.add-model__file-wrap {
  position: relative;
  margin-bottom: 35px;
  margin-top: 35px;
}

.add-model__file-label {
  display: flex;
  width: 100%;
  height: 63px;
  padding: 20px;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 15px;
  line-height: 17px;
  color: rgba(112, 112, 112, 0.5);
  border: 1px dashed rgba(112, 112, 112, 0.5);
  border-radius: 5px;
}

.add-model__file-label.highlight {
  color: #5ea9ff;
  border: 1px dashed #5ea9ff;
}

.add-model__file-label:hover {
  cursor: pointer;
  color: #5ea9ff;
  border: 1px dashed #5ea9ff;
}

.add-model__file-input {
  position: absolute;
  top: 100%;
  left: 50%;
}

.add-model__text-name {
  transition: all 0.2s ease;
  position: absolute;
  top: 50%;
  left: 15px;
  font-weight: 400;
  font-size: 15px;
  line-height: 17px;
  color: #707070;
  transform: translateY(-50%);
  opacity: 0.5;
}

.add-model__text-input {
  width: 100%;
  padding: 33px 15px 15px;
  font-weight: 400;
  font-size: 15px;
  line-height: 17px;
  background-color: transparent;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
}

.add-model__text-input.fill + .add-model__text-mask + .add-model__text-name {
  top: 14px;
  font-size: 13px;
  line-height: 15px;
  transform: translateY(0);
  opacity: 1;
}

.add-model__text-input.mask + .add-model__text-mask {
  display: block;
}

.add-model__text-input:hover {
  background-color: #fff;
}

.add-model__text-input:focus {
  outline: none;
  border: 1px solid #5ea9ff;
  background-color: #fff;
}

.add-model__text-input.error {
  border: 1px solid #ff0000;
}

.add-model__email-error, .add-model__brand-error, .add-model__model-error {
  display: none;
  margin: 0;
  padding-bottom: 12px;
  margin-top: -4px;
  font-size: 13px;
  line-height: 15px;
  color: #ff0000;
  text-align: left;
}

.add-model__email-error.show, .add-model__brand-error.show, .add-model__model-error.show {
  display: block;
}

.add-model__text-mask {
  position: absolute;
  bottom: 15px;
  left: 16px;
  display: none;
  font-size: 15px;
  line-height: 17px;
  color: #707070;
  opacity: 0.3;
}

.add-model__agreement {
  position: relative;
  display: inline-flex;
  justify-content: center;
  margin-bottom: 35px;
}

.add-model__agreement-label {
  position: relative;
  display: block;
  padding-left: 44px;
  padding-top: 4px;
  min-height: 24px;
  font-size: 15px;
  line-height: 17px;
  color: rgba(112, 112, 112, 0.5);
  text-align: left;
}

.add-model__agreement-label:hover {
  cursor: pointer;
  color: #5ea9ff;
}

.add-model__agreement-label:focus {
  color: rgba(112, 112, 112, 0.5);
}

.add-model__agreement-label:active {
  color: rgba(112, 112, 112, 0.5);
}

.add-model__agreement-label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);
  border: 1px solid #5ea9ff;
  border-radius: 3px;
}

.add-model__agreement-input {
  position: absolute;
  top: 100%;
  left: 12px;
}

.add-model__agreement-input:checked + .add-model__agreement-label::before {
  background-color: #5ea9ff;
  background-image: url("../img/checked-white.svg");
  background-position: center center;
}

.add-model__agreement-input.error + .add-model__agreement-label::before {
  border: 1px solid #ff0000;
}

.add-model__submit {
  display: block;
  margin: 0 auto;
}

.add-model__to-main {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  padding: 20px 60px 19px;
  color: #fff;
}

.add-model__file-progress {
  position: absolute;
  top: 1px;
  left: 1px;
  display: none;
  align-items: center;
  justify-content: center;
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  background-color: #f7f7f7;
  border-radius: 5px;
}

.add-model__file-progress.show {
  display: flex;
}

.add-model__bar-bg {
  position: relative;
  width: calc(100% - 30px);
  height: 5px;
  background-color: #eee;
}

.add-model__bar-current {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #5ea9ff;
}

.add-model__file-result {
  position: absolute;
  top: 1px;
  left: 1px;
  display: none;
  align-items: center;
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  padding-right: 50px;
  background-color: #f7f7f7;
  border-radius: 5px;
  overflow: hidden;
}

.add-model__file-result.show {
  display: flex;
}

.add-model__file-image-wrap {
  width: 63px;
  min-width: 63px;
  height: 100%;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ddd;
}

.add-model__file-name {
  margin: 0;
  font-size: 15px;
  line-height: 17px;
  color: #707070;
  text-overflow: ellipsis;
  overflow: hidden;
}

.add-model__file-close {
  position: absolute;
  top: 50%;
  right: 15px;
  display: block;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);
  border: none;
  background-color: transparent;
  background-image: url("../img/search-close.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.add-model__file-close:hover {
  cursor: pointer;
  opacity: 0.75;
}

.add-model__file-close:active {
  opacity: 0.5;
}

.add-model__file-close:focus {
  outline: none;
}

@media (max-width: 639px) {
  .add-model__desc--sended {
    max-width: 400px;
  }
}

@media (max-width: 479px) {
  .add-model__submit {
    width: 100%;
    max-width: 360px;
  }
  .add-model__to-main {
    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
  }
}

.search-result {
  margin-bottom: 24px;
}

.search-result:not(:first-of-type) .device__title-wrap {
  display: none;
}

.search-result .device__block {
  margin-bottom: 16px;
}

@media (max-width: 639px) {
  .search-result:not(:first-of-type) .device__title-wrap {
    display: block;
  }
}
